import { translations, type Language, type TranslationKey } from './translations';
import type { Cookies } from '@sveltejs/kit';

export { translations, type Language, type TranslationKey };

export const availableLanguages: Language[] = ['en', 'nl', 'fr'];

export function getTranslation(lang: Language, key: TranslationKey): string {
	return translations[lang][key];
}

export function getLanguageFromCookies(cookies: Cookies): Language {
	return (cookies.get('language') as Language) || 'nl';
}
